import React from 'react';
import Parcel from 'single-spa-react/parcel';
import history from './services/history';
import './styles.css';

function src() {

  var token = localStorage.getItem('token')

  const removeToken = () => {
    localStorage.removeItem('token')
    history.push('/')
  }

  if(!(token && token === 'testando')){
    history.push('/')
  }

  return (
    <div className="app-container">
      <div>
        <strong> vc está dentro do app</strong>
        <div style={{marginTop: '40x'}}>
          <button onClick={removeToken} style={{background: 'red', border: 'none', padding: '15px 0', color: '#fff'}}>Remover token</button>
        </div>
      </div>

      <Parcel 
        config={() => System.import('@atlas/vue-todo-list')}
      />

      <Parcel 
        config={() => System.import('@atlas/atlas-extract')}
      />

      <Parcel 
        config={() => System.import('@atlas/parcel-angular')}
      />
    </div>

  )
}

export default src;